//       .sidebar-menu {
//         .nav-link {
//           padding: 15px 10px;
//           text-align: center;
//           // text-align: center;
//           span {
//             display: none;
//           }
//           > i {
//             margin: 0 auto;
//             font-size: 20px;
//           }
//           .icon-jiantouyou {
//             display: none;
